@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&family=Roboto+Slab&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&family=Jersey+25&family=Roboto+Slab&display=swap');
@import url('https://fonts.googleapis.com/css2?family=EB+Garamond&family=Inconsolata:wght@500&family=Jersey+25&family=Roboto+Slab&display=swap');

:root{
    --button-color: rgb(22, 30, 63);
    --font-style: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

body{
    margin: 0;
    padding: 0;
}

.main-body-wrapper{
    background-color: rgb(22, 30, 63);
    height: 100dvh;
    width: 100dvw
}

.navbar-component-wrapper{
    height: 8%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar-wrapper{
    height: 100%;
    width: 100%;
    background-color: rgb(49, 61, 112);
    box-shadow: 5px 5px 50px rgb(49, 61, 112);
    display: flex;
    align-items: center;
    flex-direction: row;
    /* border-bottom-left-radius: 20px; */
    /* border-bottom-right-radius: 20px; */
}

.navbar-logo-container{
    height: 100%;
    width: 10%;
    /* background-color: antiquewhite; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    letter-spacing: 1px;
}

.navbar-items-container{
    height: 100%;
    width: 72%;
    /* background-color: aquamarine; */
    display: flex;
    align-items: center;
    justify-content: center;
    gap:40px;
    flex-direction: row;
    font-size: 20px;
    list-style: none;
    padding-left: 10px;
    font-family: "Roboto Slab", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
}


.navbar-items-container>li>a{
    text-decoration: none; 
    color: rgb(207, 207, 207);
    font-family: var(--font-style);
}

.navbar-items-container>li>a:hover{
    color: white;
}

.navbar-button-container{
    height: 100%;
    width: 18%;
    /* background-color: tomato; */
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 30px;
    padding-right: 3%;
}

.navbar-login-button{
    height: 60%;
    width: 80px;
    border: none;
    border-radius: 5px;   
    font-size: 15px;
    background-color: var(--button-color);
    color: white;
    font-family: var(--font-style);
    transition: 0.2s linear;
    cursor: pointer;
}

.navbar-login-button:hover{
    background-color:rgb(36, 49, 104);
    border: 1px solid white;
    box-shadow: 5px 5px 30px var(--button-color) ;
}

.landing-home-page{
    height: 89%;
    width: 100%;
    /* background-color: aqua; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}

.landing-page-content-part{
    height: 100%;
    width: 60%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* padding-top: 20px; */
}

.landing-page-logo-part{
    height: 100%;
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.landing-page-logo-part-container{
    height: 70%;
    width: 100%;
    position: relative;
    overflow: hidden;
}

.landing-page-logo-first-container{
    position: absolute;
    height: 300px;
    width: 300px;
    background-color: #2234ae;
    background-image: linear-gradient(315deg, #2234ae 0%, #161256 74%);
    border-radius: 20%;
    transform: rotate(50deg);
    z-index: 4;
    margin: 70px 0px 0px 100px;
    transition: ease-in-out .5s;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.landing-page-logo-second-container{
    position: absolute;
    height: 300px;
    width: 300px;
    background-color: #2234ae;
    background-image: linear-gradient(315deg, #161256 0%, #2234ae 74%);
    border-radius: 20%;
    transform: rotate(50deg);
    z-index: 2;
    margin: 80px 0px 0px 150px;
    transition: ease-in-out .5s;
    display: flex;
    align-items: center;
    justify-content: center;
}

.landing-page-logo-second-container:hover{
    box-shadow:10px 5px 40px white;
    transform: rotate(230deg);
    z-index: 10;
}

.landing-page-logo-first-container:hover{
    box-shadow:10px 5px 40px white;
    transform: rotate(-220deg);
    /* z-index: 10; */
}

.landing-page-logo-rotator{
    position: fixed;
    height: 90%;
    width: 90%;
    /* background-color: red; */
    display: flex;
    align-items: center;
    justify-content: center;
    transition: ease-in-out .5s;
}

.landing-page-logo-rotator-text{
    position: fixed;
    height: 100%;
    width: 100%;
    /* background-color: red; */
    display: flex;
    align-items: center;
    justify-content: center;
    transition: ease-in-out .5s;
}

.landing-page-logo-rotator:hover{
    transform: rotate(270deg);
    /* height: ; */
}

.landing-page-logo-rotator-text:hover{
    transform: rotate(130deg);
    /* height: ; */
}

.landing-page-first-container-logo{
    /* position: absolute; */
    height: 150px;
    width: 150px;
    /* transform: translate(-50%, -50%); */
    transform: rotate(-50deg);
}

.landing-page-first-container-text{
    transform: rotate(-50deg);
}

.landing-page-content-header{
    height: 38%;
    width: 80%;
    padding-top: 55px;
    /* background-color: red; */
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.landing-page-content-info{
    height: 55%;
    width: 80%;
    padding-bottom: 100px;
    /* background-color: black; */
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.landing-page-content-info>p{
    color:white;
    font-size: 22px;
    line-height: 1.6;
    font-family: "EB Garamond", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 1px;
}

.landing-page-content-header>h1{
    /* background-color: red; */
    font-family: "Jersey 25", sans-serif;
    font-weight: 400;
    font-style: normal; 
    font-size: 160px;
    animation: headerColorChange 2s infinite ;
    color: white;
}

.landing-page-footer-container{
    height:3% ;
    width: 100%;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: row;
}

@keyframes headerColorChange {
    0%{
        color: rgb(8, 255, 234);
    }
    20%{
        color: rgb(0, 191, 255);
    }
    60%{
        color: rgb(149, 0, 255);
    }
    100%{
        color: rgb(247, 78, 171);
    }
}

.footer-component-copyright-segment{
    height: 100%;
    width: 100%;
    /* background-color: red; */
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 11px;
    font-family: "Roboto Slab", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    color: white;

}

.login-auth-page-wrapper{
    display: flex;
    height: 670px;
    width: 450px;
    position: absolute;
    /* background: black; */
    top: 20px;
    /* left: -100px; */
    z-index: 30;
    transition: transform 0.4s ease-in-out;
    /* transform: translateX(0px); */
}

.login-auth-detail-container{
    height: 100%;
    width: 100%;
    border-radius: 10px;
    background-color: #2b4162;
    background-image: linear-gradient(315deg, #2b4162 0%, #12100e 74%);
    display: none;
    align-items: center;
    justify-content: center;
    color: white;
    flex-direction: column;
}

.login-auth-container-header{
    height: 15%;
    width: 100%;
    /* background-color: cyan; */
    display: flex;
    align-items: flex-end;
    justify-content: center;
    font-size: 50px;
    font-weight: 500;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    flex-direction: row;
}

.login-auth-container-close-button{
    /* background-color: r  ed; */
    height: 7%;
    width: 10%;
    float: left;
    position: absolute;
    right: 13px;
    top: 10px;
    cursor: pointer;
}

.login-auth-container-body{
    height: 75%;
    width: 100%;
    /* background-color: #161256; */
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* gap: 50px;    */
}

.login-auth-container-signUp{
    height: 10%;
    width: 100%;
    /* background-color:red; */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 15px;
}

.login-auth-body-email{
    height: 10%;
    width: 75%;
    background: transparent;
    border: none;
    outline: none;
    border-bottom: 2px solid grey;
    font-size: 15px;
    color: white;
}

.login-auth-body-button{
    height: 10%;
    width: 70%;
    background-color: transparent;
    outline: none;
    border-radius: 30px;
    border: 3px solid white;
    color: white;
    font-size: 20px;
    cursor: pointer;
    transition: ease-in-out .3s;
}

.login-auth-body-button:hover{
    transform: scale(115%,110%);
}

.login-auth-email-pass-container{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
    height:50%;
    width: 100%;
    /* background-color: cyan; */
    gap: 60px;
}